
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters, mapState } from 'vuex'

const ROLE_PRESIDENT = 'president'
const ROLE_ENTREPRENEUR = 'entrepreneur'

export default {
data() {
return {
currentRouter: 0
}
},
components: {
Tabbar,
TabbarItem
},
methods: {
handleToggle(val) {
this.currentRouter = val
}
},
computed: {
...mapState(['isHideTabbar']),
...mapGetters(['role_type']),
showTabbar() {
return ['/index', '/finance', '/supplier', '/entrepreneur', '/my'].includes(this.$route.path)
},
tabMenus() {
return [{
text: '首页',
icon: 'icon-home',
link: '/index'
}, {
text: '财务',
icon: 'icon-icon-',
link: '/finance'
}, {
text: '店铺',
icon: 'icon-mendianguanli',
link: '/supplier'
},
this.role_type === ROLE_PRESIDENT ?
{
text: '创客',
icon: 'icon-dianpu',
link: '/entrepreneur'
} : {},
{
text: '我的',
icon: 'icon-wode',
link: '/my'
}
]
}
}
}



